import vue from '../main.js'
import axios from 'axios'
let store = vue.$store

// 
if (process.env.NODE_ENV == 'development') { 
    axios.defaults.baseURL = 'https://www.hotmine.cn/miniprogram';}
else if (process.env.NODE_ENV == 'debug') { 
    // axios.defaults.baseURL = 'https://www.hotmine.cn/miniprogram';
    axios.defaults.baseURL = 'https://www.hotmine.cn/miniprogram';
} 
else if (process.env.NODE_ENV == 'production') { 
    // axios.defaults.baseURL = 'https://www.hotmine.cn/miniprogram';
    axios.defaults.baseURL = 'https://www.hotmine.cn/miniprogram';
}
//
axios.defaults.timeout = 1000;
//
axios.defaults.headers.post['Content-Type'] = 'application/json';
//
axios.interceptors.request.use(
    config => {
        //
        if(config.url.split('/').pop() === 'userLogin'){
            return config
        }else{
            //tokentoken
            if(uni.getStorageSync('token')){
                config.headers.roleHeader = store.state.token
                config.headers.uuid = store.state.uuid
            }
            return config
        }
    },
    error => {
        return Promise.reject(error);
    }
)
//
axios.interceptors.response.use(
    response => {
        if(response.status === 200){
            //token
            if(response.config.url.split('/').pop() === 'userLogin'){
                if(response.header.roleHeader){
					uni.setStorage({
						key: 'token',
						data: response.header.roleHeader 
					})
                }
                if(response.header.uuid){
					uni.setStorage({
						key: 'uuid',
						data: response.header.uuid
					})
                }
            }
            return Promise.resolve(response)
        }else {
            Promise.reject(response)
        }
    },
    error => {
        if(error.response.status){
            switch (error.response.status){
                case 401:
					uni.showToast({
						title: '',
						icon: 'none',
						success() {
							store.dispatch('login')
						}
					})
                break;
				default: 
					uni.showToast({
						title: ''
					})
            }
        }
        return Promise.reject(error.response)
    }
)

axios.defaults.adapter = function(config) { //uniapp
    return new Promise((resolve, reject) => {
        var settle = require('axios/lib/core/settle');
        var buildURL = require('axios/lib/helpers/buildURL');
        uni.request({
            method: config.method.toUpperCase(),
            url: config.baseURL + buildURL(config.url, config.params, config.paramsSerializer),
            header: config.headers,
            data: config.data,
            dataType: config.dataType,
            responseType: config.responseType,
            sslVerify: config.sslVerify,
            complete: function complete(response) {
                response = {
                    data: response.data,
                    status: response.statusCode,
                    errMsg: response.errMsg,
                    header: response.header,
                    config: config
                };

                settle(resolve, reject, response);
            }
        })
    })
}

//get
export function get(url,param){
    return new Promise( (resolve,reject) => {
        axios.get(url,{
            params: param
        }).then(res => {
            resolve(res.data)
        }).catch( err => {
            reject(err.data)
        })
    })
}
//post
export function post(url,param){
    return new Promise( (resolve,reject) => {
        axios.post(url,param).then(res => {
            resolve(res.data)
        }).catch( err => {
            reject(err.data)
        })
    })
}

